import './Chart.css'


import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    TimeScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';
import annotationPlugin from 'chartjs-plugin-annotation';

import 'chartjs-adapter-date-fns';
import {es} from 'date-fns/locale';

const my_data = require("../../data/export.json");

ChartJS.register(
    CategoryScale,
    LinearScale,
    TimeScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
    annotationPlugin
);


export const options = {
    responsive: true,
    interaction: {
        intersect: false,
        mode: 'nearest',
        axis: 'x'
    },
    plugins: {
        legend: {
            position: 'top',
        },
        title: {
            display: false,
            text: 'Elecciones Generales de España',
            font: {
                size: '25px'
            }
        },
        tooltip: {
            bodyFont: {
                size: 14
            }
        },
        annotation: {

            // events: ['dblclick'],
            // drawTime: 'afterDatasetsDraw',
            annotations: {
                'forecast': {
                    type: 'box',
                    drawTime: "beforeDatasetsDraw",
                    xMin: new Date(de_ofuscate(my_data["last_train"], 1000, 1)).toISOString().split('T')[0],
                    borderWidth: 0,
                    backgroundColor: 'rgba(204, 226, 239, 0.6)',
                    label: {
                        display: true,
                        content: 'PREVISIÓN',
                        position: {
                            y: 'start'
                        },
                        color: 'rgba(21,127,179,255)',
                        font: {
                            size: '15px'
                        }
                    }
                },
                label1: {
                    type: 'label',
                    // drawTime: "beforeDatasetsDraw",
                    // xValue: 0,
                    yValue: 37,
                    adjustScaleRange: false,
                    backgroundColor: 'white', //'rgba(245,245,245)',
                    content: `© ${new Date().getFullYear()} - Elecciones Futuras - https://eleccionesfuturas.github.io/web/`,
                    font: {
                        size: 13
                    }
                }
            }
        },
        crosshair: {
            line: {
                color: '#F66',  // crosshair line color
                width: 1        // crosshair line width
            },
            sync: {
                enabled: false,            // enable trace line syncing with other charts
                group: 1,                 // chart group
                suppressTooltips: false   // suppress tooltips when showing a synced tracer
            },
            zoom: {
                enabled: false,                                      // enable zooming
                zoomboxBackgroundColor: 'rgba(66,133,244,0.2)',     // background color of zoom box
                zoomboxBorderColor: '#48F',                         // border color of zoom box
                zoomButtonText: 'Reset Zoom',                       // reset zoom button text
                zoomButtonClass: 'reset-zoom',                      // reset zoom button class
            },
            callbacks: {
                beforeZoom: () => function(start, end) {                  // called before zoom, return false to prevent zoom
                    return true;
                },
                afterZoom: () => function(start, end) {                   // called after zoom
                }
            }
        }
    },
    elements: {
        point:{
            radius: 2
        }
    },
    scales: {
        x: {
            type: 'time',
            time: {
                unit: 'month'
            },
            adapters: {
                date: {
                    locale: es
                }
            }
            // ticks: {
            //     source: 'labels'
            // }
        },
        y: {
            type: 'linear',
            display: true,
            position: 'right',
        }
    }
};

const colors = {
    'pp': 'rgba(0,123,181,255)',
    'psoe': 'rgba(227,34,40,255)',
    'vox': 'rgba(103,188,44,255)',
    'sumar': '#e51c55',
    'podemos': 'rgba(150,23,167,255)',
    'salf': '#7a5a44',
    'ciudadanos': 'rgba(255,79,0,255)',
    'otros_blancos': 'grey'
};

function de_ofuscate(value, mul, div) {
    // orig ((int(value) ^ 11) -17) * 3
    if(value === undefined || value == null) {
        return null;
    }
    return (((value / 3) + 17)  ^ 11) * mul / div;
}

export const data = {
    labels: my_data["labels"].map((v) => de_ofuscate(v, 1000, 1)),
    datasets: Object.keys(my_data["datasets"])
        .map((k) => [k, Object.values(my_data["datasets"][k])])
        .map(([k, v]) => ({
            label: k,
            data: v.map((v) => de_ofuscate(v, 1, 100)),
            borderColor: colors[k],
            backgroundColor: colors[k],
            borderWidth: 1
        }))
};


const Chart = () => {
  return (
      <section className='section contact center' id='chart'>
          <h2 className='section__title'>Elecciones Generales de España</h2>
         <Line options={options} data={data} />
      </section>
  )
}

export default Chart
