import ScrollToTop from './components/ScrollToTop/ScrollToTop'
import Contact from './components/Contact/Contact'
import Footer from './components/Footer/Footer'
import './App.css';

import React from 'react';

import ReactGA from "react-ga4";
const TRACKING_ID = "G-GX3ZMZWHJR";
ReactGA.initialize(TRACKING_ID);

function App() {
    // https://github.com/rajshekhar26/cleanfolio
    //const [{ themeName }] = useContext(ThemeContext)
    //disabled dark mode for now
    // <div id='top' className={`${themeName} app`}>

    ReactGA.send("pageview");

    document.onmousedown = function (event) {
        if (!event) {event = window.event;}
        // console.log("mousedown "+ event.target, event);
        // Post the event object here.

        let eventStr = '' + event.target;
        window.umami(eventStr);

        ReactGA.event({
            category: 'User',
            action: 'click',
            label: eventStr
        });
    };

  return (
      <div id='top' className={'light app'}>
          <Header />
          <main>
              <Chart />
              {/*<About />*/}
              {/*<Projects />*/}
              {/*<Skills />*/}
              <Contact />
          </main>

          <ScrollToTop />
          <Footer />
      </div>
  );
}

export default App;
